.main-content {
  padding-top: 26px;
  background-color: $default-color;

  &--spread {
    min-height: 100vh;
  }

  &__categories {
    margin: 0 auto;
    max-width: 1775px;
  }

  &__goods {
    margin: 0 auto;
    max-width: 454px * 4;

    @include breakpoint(454px * 4 + 32) {
      max-width: 454px * 3;
    }

    @include breakpoint(454px * 3 + 32) {
      max-width: 454px * 2;
    }

    @include breakpoint(454px * 2 + 32) {
      max-width: 454px;
    }
  }
}

.categories {
  display: flex;
  padding: 29px 25px 58px;

  &__input-item {
    display: none;

    &:checked + .categories__input-label {
      color: $primary-color;
    }
  }

  &__input-label {
    margin-right: 43px;
    transition: color 0.2s;
    color: $primary-font-color;
    @include fontsize(13);
    font-family: $tertiary-font-family;
    text-transform: uppercase;
    cursor: pointer;
    line-height: 1em;

    &:hover {
      color: $primary-color;
    }
  }
}

.goods {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  padding: 0 8px 40px;

  &__item {
    position: relative;
    display: flex;
    margin: 0 17px 34px;
    width: 420px;

    &:hover {
      .goods__details {
        opacity: 1;
        visibility: visible;
        transition-delay: 0s;
      }

      .goods__details::before {
        visibility: visible;
        opacity: 1;
        transition-delay: 0s;
        pointer-events: none;
      }
    }
  }

  &__image-wrapper {
    position: relative;
    @include size(100%, 0);
    padding-top: 500 / 420 * 100%;
    background-color: $light-bg-color;
    cursor: pointer;
  }

  &__image {
    position: absolute;
    bottom: 50%;
    right: 50%;
    display: block;
    max-height: 100%;
    max-width: 100%;
    transform: translate(50%, 50%);
  }

  &__price {
    position: absolute;
    top: 12px;
    left: 15px;
    color: #9a9a9a;
    @include fontsize(21);
    font-weight: 400;
    letter-spacing: 0.02em;

    &-sym {
      vertical-align: super;
      font-size: 0.5em;
    }
  }

  &__details {
    position: absolute;
    top: 100%;
    left: 0;
    z-index: 1;
    visibility: hidden;
    opacity: 0;
    padding: 19px 25px 25px;
    width: 100%;
    box-sizing: border-box;
    transition: opacity 0.2s, visibility 0s linear 0.2s;
    border-top: 1px solid $border-color;
    background-color: $default-color;

    &::before {
      position: absolute;
      bottom: 0;
      left: 0;
      display: block;
      visibility: hidden;
      opacity: 0;
      @include size(100%);
      padding-top: calc(100% * 500 / 420 + 1px);
      transition: opacity 0.2s, visibility 0s linear 0.2s;
      content: '';
      box-shadow: 0 0 24px rgba(0, 0, 0, 0.14);
    }
  }

  &__title {
    margin: 0;
    color: $primary-font-color;
    @include fontsize(13);
    text-transform: uppercase;
    text-align: center;

    &-price-sym {
      margin-left: 3px;
      vertical-align: super;
      font-size: 0.68em;
    }
  }

  &__description {
    padding: 0 30px;
    color: $tertiary-font-color;
    font-family: $secondary-font-family;
    @include fontsize(15);
    text-align: center;
    line-height: 1.47em;
  }

  &__actions {
    display: flex;
    justify-content: space-between;
  }

  &__input {
    @include size(67px, 41px);
    padding: 8px;
    box-sizing: border-box;
    border: 1px solid $aux-color;
    @include fontsize(16);

    &:invalid {
      border-color: $danger-color;
      outline-color: $danger-color;
      color: $danger-color;
    }

    &::placeholder {
      color: $aux-color;
      @include fontsize(16);
      font-family: Arial, Helvetica, sans-serif;
      font-style: italic;
    }
  }

  &__button {
    margin-left: 10px;
    @include size(177px, 41px);
  }
}

.button {
  padding: 10px;
  background-color: $dark-bg-color;
  opacity: 0.95;
  color: $default-color;
  border: none;
  cursor: pointer;
  transition: opacity 0.1s;
  @include fontsize(11);
  letter-spacing: 0.25em;
  text-transform: uppercase;

  &:hover {
    opacity: 1;
  }

  &:active {
    transform: translateY(1px);
  }
}

.button__icon {
  margin-right: 9px;
}

@include breakpoint(tablet) {
  .main-content {
    padding-top: 0;

    &__categories {
      padding: 25px;
    }
  }
}

@include breakpoint(portrait-phone) {
  .goods {
    &__price {
      @include fontsize(18);
    }

    &__input {
      width: 60px;
    }

    &__button {
      width: 151px;
      letter-spacing: 0.12em;
    }
  }
}
