.header {
  display: flex;
  flex-direction: column;

  &__top-bar {
    display: flex;
    justify-content: center;
    min-height: 45px;
    background-color: $dark-bg-color;
    @include fontsize(13);
    line-height: 1.5em;
  }

  &__top-bar-container {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 1170px;
  }

  &__register {
    margin: 0 17px;
    transition: color 0.2s;
    color: $secondary-font-color;
    text-decoration: none;
    text-transform: capitalize;
    font-weight: 500;

    &:hover {
      color: $default-color;
    }
  }

  &__auth {
    @extend .header__register;
  }

  &__cart {
    position: relative;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 0 40px 0 50px;
    margin-left: 65px;
    height: 100%;
    min-width: 147px;
    box-sizing: border-box;
    transition: background-color 0.2s;
    background-color: $primary-color;
    color: $default-color;
    text-decoration: none;

    &::before {
      position: absolute;
      top: 50%;
      left: 23px;
      transform: translateY(-50%);
      content: '\e900';
      font-family: 'icomoon';
      @include fontsize(20);
    }

    &::after {
      position: absolute;
      top: 50%;
      right: 20px;
      transform: translateY(-50%);
      content: '\e901';
      font-family: 'icomoon';
      font-weight: 300;
    }

    &:hover {
      background-color: $primary-color-hover;
    }
  }

  &__bottom-bar {
    position: relative;
    display: flex;
    justify-content: center;
    min-height: 103px;
    background-color: $default-color;
  }

  &__bottom-bar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 25px;
    width: 1787px;
    border-bottom: 1px solid $border-color;
  }

  &__logo {
    padding: 10px 25px 22px 0;
  }

  &__menu {
    width: 628px;
  }
}

.header__menu-toggler {
  position: absolute;
  top: 50%;
  right: 17px;
  z-index: 200;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  @include size(46px);
  padding: 0;
  box-sizing: border-box;
  border: none;
  background: none;
  transform: translateY(-50%);
  cursor: pointer;
}

.header__menu-toggler-inner,
.header__menu-toggler-inner::before,
.header__menu-toggler-inner::after {
  display: block;
  @include size(30px, 2px);
  border-radius: 1px;
  background-color: $primary-color;
  transition: transform 0.2s, background-color 0.2s;
}

.header__menu-toggler-inner {
  position: relative;
}

.header__menu-toggler-inner::before,
.header__menu-toggler-inner::after {
  position: absolute;
  left: 0;
  content: '';
}

.header__menu-toggler-inner::before {
  bottom: 9px;
}

.header__menu-toggler-inner::after {
  top: 9px;
}

.header__menu-toggler--open {
  & .header__menu-toggler-inner {
    background-color: transparent;
  }

  & .header__menu-toggler-inner::before {
    transform: translate(0, 9px) rotate(45deg);
    background-color: $primary-color;
  }

  & .header__menu-toggler-inner::after {
    transform: translate(0, -9px) rotate(-45deg);
  }
}

.logo {
  margin: 0;
  line-height: 1em;
  white-space: nowrap;

  &__link {
    color: $title-color;
    text-decoration: none;
    text-transform: uppercase;
    @include fontsize(24);
    font-weight: 600;
    letter-spacing: 0.04em;

    &--bold {
      font-weight: 700;
    }
  }
}

.menu {
  display: flex;
  align-items: center;

  &__list {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-grow: 1;
    padding: 0 15px;
    margin: 31px 0 0;
  }

  &__item {
    position: relative;
    list-style: none;
  }

  &__item--dropdown:hover {
    .menu__dropdown-content {
      display: block;
      animation: fadeIn 0.3s;
    }
  }

  &__dropdown-content {
    position: absolute;
    top: 43px;
    z-index: 1;
    display: none;
    padding: $dropdown-padding;
    background-color: $light-bg-color;
    box-shadow: 0 17px 24px 0 rgba(0, 0, 0, 0.14);

    &--left-align {
      left: -7px;
    }

    &--right-align {
      right: -7px;
    }
  }

  &__dropdown-container {
    display: flex;
  }

  &__title {
    position: relative;
    margin: 0;
    transition: color 0.2s;
    color: $title-color;
    text-transform: uppercase;
    @include fontsize(13);
    font-weight: 600;
    cursor: pointer;

    &--dropdown {
      padding-bottom: 30px;
    }

    &--dropdown::before {
      position: absolute;
      bottom: 10px;
      left: 0;
      transition: color 0.2s;
      content: '\e901';
      color: $aux-color;
      font-family: 'icomoon';
      font-weight: 300;
    }
  }

  &__item:hover .menu__title {
    color: $primary-color;
  }

  &__item:hover .menu__title--dropdown::before {
    color: $primary-color;
  }

  &__nav-list {
    padding-right: $dropdown-padding;
    min-width: 237px;
    box-sizing: border-box;

    &:last-child {
      min-width: 172px;
      padding-right: 0;
    }
  }

  &__promo {
    margin-top: 52px;
  }

  &__link {
    display: block;
    color: inherit;
    text-decoration: none;
  }
}

.nav-list {
  &__list {
    margin: 0;
    padding: 0;
  }

  &__item {
    padding: 6px 0;
    list-style: none;
  }

  &__subtitle {
    margin: 0 0 23px;
    color: $subtitle-color;
    @include fontsize(12);
    text-transform: uppercase;
  }

  &__link {
    color: $primary-font-color;
    transition: color 0.1s;
    @include fontsize(14);
    font-weight: 400;
    line-height: 1.3em;
    text-decoration: none;

    &:hover {
      color: $primary-color;
    }
  }
}

.promo {
  padding: 53px;
  background-color: $dark-bg-color;

  &__text {
    display: block;
    text-align: center;
    color: $default-color;
    text-transform: uppercase;
    @include fontsize(36);
    line-height: 1.33em;
    letter-spacing: 0.04em;
  }

  &__text--bold {
    font-weight: 600;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@include breakpoint(tablet) {
  .header {
    &__bottom-bar-container {
      flex-direction: column;
      justify-content: center;
    }

    &__logo {
      padding: 20px 0 0;
    }
  }

  .menu {
    &__list {
      margin-top: 13px;
    }

    &__dropdown-content {
      top: 35px;
    }
  }
}

@include breakpoint(landscape-phone) {
  .header {
    &__bottom-bar {
      min-height: 70px;
    }

    &__logo {
      padding: 0;
      width: 100%;
    }

    &__menu-toggler {
      display: flex;
    }

    &__menu {
      position: fixed;
      top: 0;
      left: 100%;
      z-index: 199;
      align-items: flex-start;
      @include size(100%, 100vh);
      padding: 50px 20px 50px 50px;
      box-sizing: border-box;
      overflow-y: auto;
      transition: transform 0.3s 0.1s;
      background-color: rgba(51, 51, 51, 0.99);

      &--expanded {
        transform: translateX(-100%);
      }
    }
  }

  .menu {
    align-items: flex-start;

    &__list {
      flex-direction: column;
      padding: 0;
      margin: 0;
    }

    &__item--dropdown:hover {
      .menu__dropdown-content {
        animation: none;
      }
    }

    &__dropdown-content {
      position: static;
      display: block;
      padding: 0 0 0 10px;
      background-color: transparent;
      box-shadow: none;
    }

    &__dropdown-container {
      flex-direction: column;
    }

    &__title {
      color: $primary-color;
      cursor: default;

      &--dropdown {
        padding-bottom: 15px;
        padding-top: 20px;

        &::before {
          display: none;
        }
      }
    }

    &__nav-list {
        padding: 0;
        min-width: auto;

      &:last-child {
        min-width: auto;
      }
    }

    &__promo {
      display: none;
    }

    &__link {
      margin-top: 24px;
      margin-bottom: 10px;
    }
  }

  .menu-list {
    &__link {
      color: $default-color;
    }

    &__subtitle {
      margin: 15px 0;
      color: $default-color;
    }
  }
}

@include breakpoint(portrait-phone) {
  .header {
    &__top-bar {
      min-height: 40px;
    }

    &__register {
      margin-right: 0;
    }

    &__cart {
      margin-left: 20px;
      padding-left: 50px;
      min-width: 133px;
    }

    &__bottom-bar {
      min-height: 60px;
    }
  }

  .logo__link {
    @include fontsize(21);
  }
}
