.footer {
  display: flex;
  flex-direction: column;
  margin-top: auto;

  &__additional-info {
    display: flex;
    justify-content: center;
    background-color: $light-bg-color;
  }

  &__info-container {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    padding-top: 40px;
    width: 1820px;

    @include breakpoint(desktop) {
      text-align: center;
    }
  }

  &__nav-list {
    padding: $list-padding;
    width: 237px;
    box-sizing: border-box;
  }

  &__text-list {
    @extend .footer__nav-list;
    width: 275px;
  }

  &__copyright {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 13px 0;
    min-height: 44px;
    box-sizing: border-box;
    background-color: $dark-bg-color;
  }

  &__copyright-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    width: 1220px;

    @include breakpoint(tablet) {
      justify-content: center;
    }
  }

  &__text-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  &__copyright-text {
    padding-left: 20px;
    padding-right: 20px;
    color: $secondary-font-color;
    line-height: 1.2em;
    @include fontsize(13);
  }
}

.text-list {
  &__list {
    @extend .nav-list__list;
  }

  &__item {
    @extend .nav-list__item;
    padding-bottom: 13px;
  }

  &__subtitle {
    @extend .nav-list__subtitle;
  }

  &__text {
    color: $primary-font-color;
    @include fontsize(14);
    font-weight: 400;
    line-height: 1.35em;
  }
}
